import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-ileo-modal',
  templateUrl: './ileo-modal.component.html',
  styleUrls: ['./ileo-modal.component.scss']
})
export class IleoModalComponent implements OnInit {
  @Input() title: string;
  @Input() content: string;
  @Input() leftBtnText: string;
  @Input() rightBtnText: string;

  @Output() leftClick = new EventEmitter<any>();
  @Output() rightClick = new EventEmitter<any>();

  constructor() {
    this.title = "";
    this.content = "";
    this.leftBtnText = "";
    this.rightBtnText = "";
  }

  ngOnInit(): void {

  }

  innerLClick(event: any) {
    this.leftClick?.emit(event);
  }

  innerRClick(event: any) {
    this.rightClick?.emit(event);
  }
}
