import { Component, OnInit, Input } from '@angular/core';
import CommonUtils from 'src/app/util/common-util';
import { PANO_DIRECTION } from 'src/app/util/common.enum';
@Component({
  selector: 'app-pano-move-btns',
  templateUrl: './pano-move-btns.component.html',
  styleUrls: ['./pano-move-btns.component.scss']
})
export class PanoMoveBtnsComponent implements OnInit {
  PANO_DIRECTION = PANO_DIRECTION;

  @Input() disabled: boolean;

  isPanoDirectionBtnDown: boolean = false;
  longClickTimeout: any;
  longClickInterval: any;

  constructor() {
    this.disabled = true;
  }


  ngOnInit(): void {

  }

  public stopPanoMoving() {
    this.isPanoDirectionBtnDown = false;

    if (this.longClickTimeout) {
      clearTimeout(this.longClickTimeout);
    }

    if (this.longClickInterval) {
      clearInterval(this.longClickInterval);
    }
  }

  onPanoDirectionClickDown(event: any, direction: PANO_DIRECTION) {
    event.preventDefault();
    event.stopImmediatePropagation();
    if (this.disabled) { return; }
    if (this.isPanoDirectionBtnDown) {
      this.stopPanoMoving();
      return;
    }
    this.isPanoDirectionBtnDown = true;

    CommonUtils.movePanorama(direction);
    this.longClickTimeout = setTimeout(() => {
      clearTimeout(this.longClickTimeout);
      if (this.isPanoDirectionBtnDown) {
        this.longClickInterval = setInterval(() => {
          CommonUtils.keepMovePanorama(direction);
        }, 36)
      }
    }, 500);
  }

  onPanoDirectionClickUp(event: any, direction: PANO_DIRECTION) {
    event.preventDefault();
    event.stopImmediatePropagation();
    this.stopPanoMoving();
  }

  onPanoMouseMove(event: any) {
    event.preventDefault();
    event.stopImmediatePropagation();
    // this.stopPanoMoving();
  }
}
