declare const jsDrawPanorama: any;
declare const jsMovePano: any;

export default class CommonUtils {
    private static isDemoUse: boolean = true;
    public static drawPanorama(panoramaDIV: any): any {
        jsDrawPanorama(panoramaDIV);
    }

    public static movePanorama(direction: any): any {
        jsMovePano(direction);
    }

    public static keepMovePanorama(direction: any): any {
        jsMovePano(direction, true);
    }

    public static goToJinShanTempleWebsite() {
        if (this.isDemoUse) {
            window.location.href = "https://mammon.tw/";
        } else {
            window.location.href = "intent://mammon.tw/";
        }
    }
}