import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { BTN_NO, STATE } from 'src/app/util/common.enum';

@Component({
  selector: 'app-bottom-btns',
  templateUrl: './bottom-btns.component.html',
  styleUrls: ['./bottom-btns.component.scss']
})
export class BottomBtnsComponent implements OnInit {
  STATE = STATE;
  @Input() state: STATE;
  @Output() buttonsClick = new EventEmitter<BTN_NO>();

  constructor() {
    this.state = STATE.INIT;
  }

  ngOnInit(): void {
  }

  innerClickBtn1() {
    this.buttonsClick.emit(BTN_NO.BUTTON_1);
  }

  innerClickBtn2() {
    this.buttonsClick.emit(BTN_NO.BUTTON_2);
  }

  innerClickBtn3() {
    this.buttonsClick.emit(BTN_NO.BUTTON_3);
  }
}
