import { Component, OnInit, Input } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { STATE } from 'src/app/util/common.enum';

@Component({
  selector: 'app-bottom-info',
  templateUrl: './bottom-info.component.html',
  styleUrls: ['./bottom-info.component.scss']
})
export class BottomInfoComponent implements OnInit {
  STATE = STATE;
  @Input() state: STATE | undefined;

  constructor(public appService: AppService) { }

  ngOnInit(): void {
  }

  getDividerClass() {
    switch (this.state) {
      case STATE.POEM_RESULT:
        return "divider-result";
      default:
        return "divider-normal";
    }
  }

  getBottomHeightClass() {
    switch (this.state) {
      case STATE.PRAY_SELECT:
        return "bottom-height-lg";
      case STATE.POEM_RESULT:
        return "bottom-height-md";
      default:
        return "bottom-height-sm";
    }
  }

  getTitleTextClass() {
    switch (this.state) {
      case STATE.PRAY_SELECT:
      case STATE.PRAY_PREPARE:
      case STATE.PRAY_SUCCESS:
      case STATE.POEM_HAS_RESULT:
        return "title-type-1";
      case STATE.PRAY_FINISH_L:
      case STATE.PRAY_FINISH_N:
      case STATE.PRAY_NO_QUOTA:
        return "title-type-2";
      case STATE.POEM_RESULT:
        return "title-type-3";
      default:
        return "";
    }
  }

  getPoemResultScaleStyle() {
    if (this.state != STATE.POEM_RESULT) {
      return {};
    } else {
      let width: number = screen.width;

      let scale;
      if (width < 360) {
        scale = 0.97 * width / 375;
      } else {
        scale = 0.87;
      }

      return {
        "-webkit-transform": "scale(" + scale + ")",
        "-ms-transform": "scale(" + scale + ")",
        "transform": "scale(" + scale + ")"
      }
    }
  }

  getPoemHintScaleStyle() {
    let width: number = screen.width;
    if (this.state != STATE.POEM_RESULT || width >= 360) {
      return {};
    } else {


      let scale = 0.97 * width / 375;

      return {
        "-webkit-transform": "scale(" + scale + ")",
        "-ms-transform": "scale(" + scale + ")",
        "transform": "scale(" + scale + ")",
        "-webkit-transform-origin": "100% 0px",
        "-ms-transform-origin": "100% 0px",
        "transform-origin": "100% 0px"
      }
    }
  }
}
