import { Component, OnInit, ViewChild } from '@angular/core';
import { AppService, PoemDetail, PoemInfo } from 'src/app/app.service';
import { PanoMoveBtnsComponent } from 'src/app/element/pano-move-btns/pano-move-btns.component';
import CommonUtils from 'src/app/util/common-util';
import { BTN_NO, PRAY_RESULT, PRAY_STATE, PRAY_TYPE, STATE } from 'src/app/util/common.enum';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChild(PanoMoveBtnsComponent) panoMoveBtnsCmpt: PanoMoveBtnsComponent | undefined;

  STATE = STATE;
  PRAY_STATE = PRAY_STATE;
  PRAY_RESULT = PRAY_RESULT;

  currentState: STATE;
  tmpStateStr: any;

  //entry
  isInfoExpand: boolean = false;

  //panorama
  isPanoRedirectModalShown: boolean = false;

  //pray
  currentPrayState: PRAY_STATE;
  currentPrayResult: PRAY_RESULT;

  //poem
  isPoemResultModalShown: boolean = false;
  poemInfo: PoemInfo;
  poemDetail: PoemDetail;

  constructor(public appService: AppService) {
    this.currentPrayResult = PRAY_RESULT.NONE;
    this.currentState = STATE.ENTRY;
    this.currentPrayState = PRAY_STATE.NONE;

    this.poemInfo = {
      left: "", center: "", right: ""
    }

    this.poemDetail = {
      firstSecList: [],
      secondSecContent: ""
    };
  }

  ngOnInit(): void {
    setTimeout(() => {
      CommonUtils.drawPanorama(document.getElementById("panorama_div"));
    });
  }

  startPray() {
    if (this.appService.isPrayQuotaExceed()) {
      this.currentPrayState = PRAY_STATE.NONE;
      this.currentPrayResult = PRAY_RESULT.NONE;
      this.currentState = STATE.PRAY_NO_QUOTA;
    } else {
      this.appService.startPray(() => {
        this.poemInfo = this.appService.getPoem();
        this.currentPrayState = PRAY_STATE.DROP_1;
        setTimeout(() => {
          this.currentPrayState = PRAY_STATE.DROP_2;
          setTimeout(() => {
            this.currentPrayState = PRAY_STATE.FINISHED;
            this.currentPrayResult = this.appService.getPrayResult();
            setTimeout(() => {
              this.currentPrayState = PRAY_STATE.NONE;
              switch (this.currentPrayResult) {
                case PRAY_RESULT.L:
                  if (this.appService.isPrayQuotaExceed()) {
                    this.currentState = STATE.PRAY_NO_QUOTA;
                  } else {
                    this.currentPrayResult = PRAY_RESULT.NONE;
                    this.currentState = STATE.PRAY_FINISH_L;
                  }
                  break;
                case PRAY_RESULT.N:
                  if (this.appService.isPrayQuotaExceed()) {
                    this.currentState = STATE.PRAY_NO_QUOTA;
                  } else {
                    this.currentPrayResult = PRAY_RESULT.NONE;
                    this.currentState = STATE.PRAY_FINISH_N;
                  }
                  break;
                case PRAY_RESULT.SUCCESS:
                  this.currentPrayResult = PRAY_RESULT.NONE;
                  this.currentState = STATE.PRAY_SUCCESS;
                  break;
              }
            }, 1700);
          }, 400);
        }, 400);
      });
    }
  }

  getStepPaddingClass() {
    if (STATE.PRAY_SELECT == this.currentState) {
      return "step-padding-big";
    } else {
      return "step-padding-small";
    }
  }

  onEntryInfoClick() {
    this.isInfoExpand = !this.isInfoExpand;
  }

  onButtonsClick(buttonNo: BTN_NO) {
    switch (this.currentState) {
      case STATE.ENTRY:
        if (BTN_NO.BUTTON_1 == buttonNo) {
          //線上參拜
          this.currentState = STATE.PANORAMA;
        } else if (BTN_NO.BUTTON_2 == buttonNo) {
          //求籤問卦
          this.toPraySelect();
        } else if (BTN_NO.BUTTON_3 == buttonNo) {
          //no button 3
        }
        break;
      case STATE.PANORAMA:
        if (this.panoMoveBtnsCmpt) { this.panoMoveBtnsCmpt.stopPanoMoving() };
        if (this.isPanoRedirectModalShown) { return; }

        if (BTN_NO.BUTTON_1 == buttonNo) {
          //瞭解更多
          this.isPanoRedirectModalShown = true;
        } else if (BTN_NO.BUTTON_2 == buttonNo) {
          //求籤問卦
          this.toPraySelect();
        } else if (BTN_NO.BUTTON_3 == buttonNo) {
          //返回大殿
          this.backToMain();
        }
        break;
      case STATE.PRAY_SELECT:
        if (BTN_NO.BUTTON_1 == buttonNo) {
          //問功名
          this.appService.setPrayType(PRAY_TYPE.JOB);
          this.currentState = STATE.PRAY_PREPARE;
        } else if (BTN_NO.BUTTON_2 == buttonNo) {
          //求財運
          this.appService.setPrayType(PRAY_TYPE.MONEY);
          this.currentState = STATE.PRAY_PREPARE;
        } else if (BTN_NO.BUTTON_3 == buttonNo) {
          //返回大殿
          this.backToMain();
        }
        break;
      case STATE.PRAY_PREPARE:
      case STATE.PRAY_FINISH_L:
      case STATE.PRAY_FINISH_N:
        if (BTN_NO.BUTTON_1 == buttonNo) {
          //返回大殿
          this.backToMain();
        } else if (BTN_NO.BUTTON_2 == buttonNo) {
          //擲筊
          this.startPray();
        } else if (BTN_NO.BUTTON_3 == buttonNo) {
          //no button 3
        }
        break;
      case STATE.PRAY_NO_QUOTA:
        if (BTN_NO.BUTTON_1 == buttonNo) {
          //返回大殿
          this.backToMain();
        } else if (BTN_NO.BUTTON_2 == buttonNo) {
          //button2 is disabled
        } else if (BTN_NO.BUTTON_3 == buttonNo) {
          //no button 3
        }
        break;
      case STATE.PRAY_SUCCESS:
        if (BTN_NO.BUTTON_1 == buttonNo) {
          //返回大殿
          this.backToMain();
        } else if (BTN_NO.BUTTON_2 == buttonNo) {
          //查看籤詩
          this.currentState = STATE.POEM_RESULT;
        } else if (BTN_NO.BUTTON_3 == buttonNo) {
          //no button 3
        }
        break;
      case STATE.POEM_RESULT:
        if (this.isPoemResultModalShown) { return; }
        if (BTN_NO.BUTTON_1 == buttonNo) {
          //分享
          this.isPoemResultModalShown = true;
        } else if (BTN_NO.BUTTON_2 == buttonNo) {
          //解籤
          this.toPoemUnlock();
        } else if (BTN_NO.BUTTON_3 == buttonNo) {
          //返回大殿
          this.backToMain();
        }
        break;
      case STATE.POEM_UNLOCK:
        if (BTN_NO.BUTTON_1 == buttonNo) {
          //回上一頁
          this.currentState = STATE.POEM_RESULT;
        } else if (BTN_NO.BUTTON_2 == buttonNo) {
          //查看完整詩籤
          this.currentState = STATE.POEM_DETAIL;
        } else if (BTN_NO.BUTTON_3 == buttonNo) {
          //no button 3
        }
        break;
      case STATE.POEM_DETAIL:
        if (this.isPoemResultModalShown) { return; }
        if (BTN_NO.BUTTON_1 == buttonNo) {
          //回上一頁
          this.toPoemUnlock();
        } else if (BTN_NO.BUTTON_2 == buttonNo) {
          //儲存籤詩
          this.isPoemResultModalShown = true;
        } else if (BTN_NO.BUTTON_3 == buttonNo) {
          //no button 3
        }
        break;
      case STATE.POEM_HAS_RESULT:
        if (BTN_NO.BUTTON_1 == buttonNo) {
          //返回大殿
          this.backToMain();
        } else if (BTN_NO.BUTTON_2 == buttonNo) {
          //查看上回籤詩
          this.currentState = STATE.POEM_RESULT;
        } else if (BTN_NO.BUTTON_3 == buttonNo) {
          //no button 3
        }
        break;
    }
  }

  onPanoThinkLaterClick(event: any) {
    event.preventDefault();
    this.isPanoRedirectModalShown = false;
  }

  onPanoGoWebsiteClick(event: any) {
    event.preventDefault();
    CommonUtils.goToJinShanTempleWebsite();
  }

  onPoemModalOkClick(event: any) {
    event.preventDefault();
    this.isPoemResultModalShown = false;
  }

  backToMain() {
    this.currentPrayState = PRAY_STATE.NONE;
    this.currentPrayResult = PRAY_RESULT.NONE;
    this.currentState = STATE.ENTRY;
  }

  toPraySelect() {
    if (this.appService.isTodayPraySuccess()) {
      this.currentPrayState = PRAY_STATE.NONE;
      this.currentPrayResult = PRAY_RESULT.NONE;
      this.currentState = STATE.POEM_HAS_RESULT;
    } else {
      this.currentState = STATE.PRAY_SELECT;
    }
  }

  toPoemUnlock() {
    this.poemDetail = this.appService.getPoemDetail();
    this.currentState = STATE.POEM_UNLOCK;
  }

  toggleClick() {
    if (this.currentState + 1 >= 12) {
      this.currentState = 0;
    } else {
      this.currentState++;
      if (this.currentState == STATE.POEM_RESULT) {
        this.appService.startPray(() => {
          this.poemInfo = this.appService.getPoem();
          this.poemDetail = this.appService.getPoemDetail();
        });
      }
    }

    this.tmpStateStr = STATE[this.currentState];
  }

  togglePoemClick() {
    this.appService.switchPoem();
    this.poemInfo = this.appService.getPoem();
    this.poemDetail = this.appService.getPoemDetail();
  }

  togglePrayTypeClick() {
    this.appService.togglePrayType();
    this.poemInfo = this.appService.getPoem();
    this.poemDetail = this.appService.getPoemDetail();
  }
}
