import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { PRAY_RESULT, PRAY_TYPE } from './util/common.enum';

@Injectable({
  providedIn: 'root'
})
export class AppService {
  private static PRAY_MAX: number = 3;
  prayCount: number = 0;
  resultPoem: any | undefined;
  prayType: PRAY_TYPE | undefined;
  prayResult: PRAY_RESULT;
  hasPraySuccess: boolean = false;
  poemJSONData: any | undefined;
  resultPoemIndex: number = 0;

  constructor(private http: HttpClient) {
    this.prayResult = PRAY_RESULT.NONE;
  }

  startPray(callback: any) {
    let finalResult;
    let tmpResult = Math.floor(Math.random() * 4) + 1;
    if (1 == tmpResult) {
      finalResult = PRAY_RESULT.L;
    } else if (2 == tmpResult) {
      finalResult = PRAY_RESULT.N;
    } else {
      finalResult = PRAY_RESULT.SUCCESS;
      this.hasPraySuccess = true;
    }

    this.addPrayCount();
    this.prayResult = finalResult;

    this.resultPoemIndex = Math.floor(Math.random() * 59);
    this.getPoemsJSON(() => {
      this.resultPoem = this.poemJSONData[this.resultPoemIndex];
      callback();
    });
  }

  private getPoemsJSON(callback: any) {
    if (!this.poemJSONData) {
      this.http.get("./assets/json/poems.json").subscribe((data) => {
        this.poemJSONData = data;
        callback();
      });
    } else {
      callback(this.poemJSONData);
    }
  }

  switchPoem() {
    this.getPoemsJSON(() => {
      if (this.resultPoemIndex + 1 == this.poemJSONData.length) {
        this.resultPoemIndex = 0;
      } else {
        this.resultPoemIndex++;
      }
      this.resultPoem = this.poemJSONData[this.resultPoemIndex];
    });
  }

  togglePrayType() {
    if (this.prayType == PRAY_TYPE.JOB) {
      this.prayType = PRAY_TYPE.MONEY;
    } else {
      this.prayType = PRAY_TYPE.JOB;
    }
  }

  getPrayCountRemaining() {
    return AppService.PRAY_MAX - this.prayCount;
  }

  isTodayPraySuccess() {
    return this.hasPraySuccess;
  }

  isPrayQuotaExceed() {
    return (this.prayCount >= AppService.PRAY_MAX);
  }

  getPoemIndex() {
    return this.resultPoemIndex + 1;
  }

  addPrayCount() {
    this.prayCount += 1;
  }

  setPrayType(prayType: PRAY_TYPE) {
    this.prayType = prayType;
  }

  getPrayResult(): PRAY_RESULT {
    return this.prayResult;
  }

  getPoemTitle() {
    if (!this.resultPoem) { return ""; }
    return this.resultPoem.poemSimpleText;
  }

  getPoemResult() {
    if (!this.resultPoem) { return ""; }

    switch (this.prayType) {
      case PRAY_TYPE.JOB:
        return this.resultPoem.jobPoem;
      case PRAY_TYPE.MONEY:
        return this.resultPoem.moneyPoem;
      default:
        return "";
    }
  }

  getPoem(): PoemInfo {
    if (!this.resultPoem) { return { left: "", center: "", right: "" }; }
    return {
      left: this.resultPoem.poemLeft,
      center: this.resultPoem.poemCenter,
      right: this.resultPoem.poemRight
    };
  }

  getPoemDetail(): PoemDetail {
    if (!this.resultPoem) { return { firstSecList: [], secondSecContent: "" }; }
    return {
      firstSecList: this.resultPoem.firstSecList,
      secondSecContent: this.resultPoem.secondSecContent
    };
  }

  getPrayerName() {
    return "王O潔"
  }
}

export interface PoemInfo {
  left: string;
  center: string;
  right: string;
}

export interface PoemDetail {
  firstSecList: {
    title: string;
    content: string;
  }[],
  secondSecContent: string;
}