import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { STATE } from 'src/app/util/common.enum';

@Component({
  selector: 'app-step-indicator',
  templateUrl: './step-indicator.component.html',
  styleUrls: ['./step-indicator.component.scss']
})
export class StepIndicatorComponent implements OnInit, OnChanges {
  STATE = STATE;
  @Input() state: STATE | undefined;
  classObj: any = {};

  constructor() {
    this.classObj = {
      stepClass: "step1",
      dot1Class: "dot-checked",
      dot2Class: "dot-uncheck",
      dot3Class: "dot-uncheck"
    };
  }


  ngOnInit(): void {

  }

  ngOnChanges(changes: SimpleChanges) {
    let currentVal = changes?.state.currentValue;
    if (currentVal) {
      this.updateClassObj(currentVal);
    }
  }

  checkAllowShowing() {
    switch (this.state) {
      case STATE.PRAY_SELECT:
      case STATE.PRAY_PREPARE:
      case STATE.PRAY_FINISH_L:
      case STATE.PRAY_FINISH_N:
      case STATE.PRAY_NO_QUOTA:
      case STATE.PRAY_SUCCESS:
        return true;
      default:
        return false;
    }
  }

  updateClassObj(currentVal: any) {
    let result = { ...this.classObj };

    if (currentVal == STATE.PRAY_SUCCESS) {
      result.stepClass = "step3";
      result.dot1Class = "dot-checked";
      result.dot2Class = "dot-checked";
      result.dot3Class = "dot-checked";
      result.text1Class = "text-checked";
      result.text2Class = "text-checked";
      result.text3Class = "text-checked";
    } else if (currentVal == STATE.PRAY_PREPARE ||
      currentVal == STATE.PRAY_FINISH_L ||
      currentVal == STATE.PRAY_FINISH_N ||
      currentVal == STATE.PRAY_NO_QUOTA) {
      result.stepClass = "step2";
      result.dot1Class = "dot-checked";
      result.dot2Class = "dot-checked";
      result.dot3Class = "dot-uncheck";
      result.text1Class = "text-checked";
      result.text2Class = "text-checked";
      result.text3Class = "text-uncheck";
    } else {
      result.stepClass = "step1";
      result.dot1Class = "dot-checked";
      result.dot2Class = "dot-uncheck";
      result.dot3Class = "dot-uncheck";
      result.text1Class = "text-checked";
      result.text2Class = "text-uncheck";
      result.text3Class = "text-uncheck";
    }

    this.classObj = result;
  }
}
